import React, { useState } from 'react'
const Donation = () => {
    const [donateAmt, setDonateAmt] = useState('')

    const handleSubmit = e => {
        e.preventDefault()
        if (!donateAmt) return
        window.analytics.track("Donation Submitted", {
            donateAmt
        })
    }

    return (
        <form onSubmit={handleSubmit}>
            <label for="SubmitDonate">Submit a Donation
                <input 
                    placeholder="Enter an amount"
                    name="SubmitDonate"
                    value={donateAmt}
                    onChange={e => setDonateAmt(e.target.value)}
                />
            </label><br />
            <button onClick={handleSubmit}>Submit a Donation</button>    
        </form>
    )
}

export default Donation